<template>
  <div class="hello">
    <link rel="stylesheet" href="https://www.w3schools.com/w3css/4/w3.css">
    <h1>{{ msg }}</h1>
    <ul>
      <li v-for="mem in members">
        <p class="w3-card-2 w3-center">
          
          <!-- profile image -->
          <img class="round-profile-image" :src="mem.image"/><BR /><BR />
          
          <!-- name -->
          <span class="name">
            {{ mem.name + ' ' + mem.surname }}
          </span><BR />
          <span class="secondary-text">
            {{ mem.role + ' & ' + mem.title }} <BR />
            {{ mem.city + ', ' + mem.country }}
          </span>
          <BR />
          <span class="bio">
            {{'\"' + mem.bio + '\"'}}
          </span>
          <BR /><BR />

          <!-- links -->
          <ul>
            <li>
              <a :href="mem.links.facebook.profile" target="_blank">
                <img class="round-icon" :src="mem.links.facebook.icon"/>
              </a>
            </li>
            <li>
              <a :href="mem.links.github.profile" target="_blank">
                <img class="round-icon" :src="mem.links.github.icon"/>
              </a>
            </li>
            <li>
              <a :href="mem.links.instagram.profile" target="_blank">
                <img class="round-icon" :src="mem.links.instagram.icon"/>
              </a>
            </li>
            <li>
              <a :href="mem.links.twitter.profile" target="_blank">
                <img class="round-icon" :src="mem.links.twitter.icon"/>
              </a>
            </li>
          </ul>
          <BR /><BR />

        </p>
      </li>
    </ul>
  </div>
</template>

<script>
import mems from '../assets/members.json'
export default {
  name: 'founders',
  data () {
    return {
      msg: 'Welcome to LmntrX Tech',
      members: mems
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.name {
  font-size: 15px;
  font-weight: bold;
  color: #333;
}

.secondary-text {
  font-size: 12px;
  color: #333;
}

.bio {
  font-size: 14px;
  font-style: italic;
  color: green;
  padding: 10px;
}

.round-profile-image {
  height: 250px;
  width: 250px;
  border-radius: 50%;
  padding: 10px;
}

.round-icon {
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

</style>



